import React, { Component } from 'react';
import { CCard, CCardBody, CCardHeader, CCol, CRow, CButton, CInput, CFormGroup, CInputRadio, CLabel, CInputGroup, CInputGroupAppend, CBadge } from '@coreui/react';
import { CIcon } from '@coreui/icons-react';

import usersData from './UsersData'
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import { TextField } from '@material-ui/core';

const getBadge = (status) => {
    switch (status) {
        case 'Active': return 'success'
        case 'Inactive': return 'secondary'
        case 'Pending': return 'warning'
        case 'Banned': return 'danger'
        default: return 'primary'
    }
}

class PlanVersion extends Component {
    constructor(props) {
        super(props)
        this.state = {
            users: [],
            userDetails: [],
            name: "",
            email: "",
            username: "",
            membership: "",
            DataRegistered: "",
            paymentMethod: "",
            renewDate: "",
        }
    }

    componentDidMount = () => {
        let { users, userDetails, name, email, username, membership, DataRegistered, paymentMethod, renewDate } = this.state;
        users = usersData.find(user => user.id.toString() === this.props.match.params.id)

        userDetails = users ? users : [['id', (<span><CIcon className="text-muted" name="cui-icon-ban" /> Not found</span>)]]
        if (users) {
            name = users.name;
            email = users.email;
            username = users.username;
            membership = users.membership;
            DataRegistered = users.DataRegistered;
            paymentMethod = users.paymentMethod;
            renewDate = users.renewDate;
        }
        this.setState({ users, userDetails, name, email, username, membership, DataRegistered, paymentMethod, renewDate })

    }
    onRefoundConfirm = () => {
        confirmAlert({
            title: 'Confirm to submit',
            message: 'Are you sure to do this.',
            buttons: [
                {
                    label: 'Yes',
                    onClick: () => alert('Click Yes')
                },
                {
                    label: 'No',
                    onClick: () => alert('Click No')
                }
            ]
        });
    }

    onChangeName = (e) => { this.setState({ name: e.target.value }) }
    onChangeEmail = (e) => { this.setState({ email: e.target.value }) }
    onChangeUsername = (e) => { this.setState({ username: e.target.value }) }
    onChangeMembership = (e) => { this.setState({ membership: e.target.value }) }
    onChangeDateRegistred = (e) => { this.setState({ DataRegistered: e.target.value }) }
    onChangePayment = (e) => { this.setState({ paymentMethod: e.target.value }) }
    onChangeRenewDate = (e) => { this.setState({ renewDate: e.target.value }) }


    render() {

        return (
            <div className="animated fadeIn">
                <CRow>
                    <CCol lg={2}>
                        <span style={{ fontSize: '25px' }}>{this.state.userDetails.username}</span>&nbsp;<CBadge color={getBadge(this.state.userDetails.subscription)}>
                            {this.state.userDetails.subscription}
                        </CBadge>
                    </CCol>
                    <CCol lg={9}>&nbsp;</CCol>
                    <CCol lg={1}><CButton color="info" onClick={this.onRefoundConfirm}>Update User </CButton></CCol>
                </CRow><br />
                <CRow>
                    <CCol lg={12}>
                        <CCard>
                            <CCardHeader>
                                {/* User id: {this.props.match.params.id} */}
                User information
              </CCardHeader>
                            <CCardBody>
                                <CRow>
                                    <CCol lg={4}>
                                        <p>Full Name<br />
                                            <TextField
                                                fullWidth
                                                // helperText="Please specify the first name"
                                                label="Full Name"
                                                margin="dense"
                                                name="name"
                                                onChange={this.onChangeName}
                                                required
                                                value={this.state.name}
                                                variant="outlined"
                                            />
                                        </p>
                                        <p>Email<br />
                                            {/* <CInput className="form-control" id="bcc2" type="email" placeholder="Type email" value={this.state.userDetails.email} /> */}
                                            <TextField
                                                fullWidth
                                                // helperText="Please specify the first name"
                                                label="email"
                                                margin="dense"
                                                name="email"
                                                onChange={this.onChangeEmail}
                                                required
                                                value={this.state.email}
                                                variant="outlined"
                                            />
                                        </p>
                                        <p>Username<br />
                                            {/* <CInput className="form-control" id="bcc3" type="email" placeholder="Type username" value={this.state.userDetails.username} /> */}
                                            <TextField
                                                fullWidth
                                                // helperText="Please specify the first name"
                                                label="username"
                                                margin="dense"
                                                name="username"
                                                onChange={this.onChangeUsername}
                                                required
                                                value={this.state.username}
                                                variant="outlined"
                                            />
                                        </p>
                                        <p>Membership<br />
                                            <CFormGroup row>
                                                <CCol md="12">
                                                    <CInputGroup>
                                                        <CInput type="email" id="input2-group2" name="input2-group2" placeholder="Membership" value={this.state.membership} onChange={this.onChangeMembership} />
                                                        <CInputGroupAppend>
                                                            <CButton type="button" color="danger" onClick={this.onRefoundConfirm}>Cancel</CButton>
                                                        </CInputGroupAppend>
                                                    </CInputGroup>
                                                </CCol>
                                            </CFormGroup>
                                        </p>
                                    </CCol>
                                    <CCol lg={2}>
                                        <p>Device</p>
                                        <CFormGroup variant="checkbox">
                                            <CInputRadio className="form-check-input" id="radio1" name="radios" value="option1" checked />
                                            <CLabel variant="checkbox" htmlFor="radio1">Apple</CLabel>
                                        </CFormGroup>
                                        <CFormGroup variant="checkbox">
                                            <CInputRadio className="form-check-input" id="radio2" name="radios" value="option2" />
                                            <CLabel variant="checkbox" htmlFor="radio2">Android</CLabel>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol lg={3}>
                                        <p>Data Registered<br />
                                            {/* <CInput className="form-control" id="bcc5" type="email" placeholder="Type DataRegistered" value={this.state.userDetails.DataRegistered} /> */}
                                            <TextField
                                                fullWidth
                                                // helperText="Please specify the first name"
                                                label="Data Registered"
                                                margin="dense"
                                                name="DataRegistered"
                                                onChange={this.onChangeDateRegistred}
                                                required
                                                value={this.state.DataRegistered}
                                                variant="outlined"
                                            />
                                        </p>
                                        <p>Payment Method<br />
                                            {/* <CInput className="form-control" id="bcc6" type="email" placeholder="Type paymentMethod" value={this.state.userDetails.paymentMethod} /> */}
                                            <TextField
                                                fullWidth
                                                // helperText="Please specify the first name"
                                                label="paymentMethod"
                                                margin="dense"
                                                name="paymentMethod"
                                                onChange={this.onChangePayment}
                                                required
                                                value={this.state.paymentMethod}
                                                variant="outlined"
                                            />
                                        </p>
                                    </CCol>
                                    <CCol lg={3}>
                                        <p>Renew Date<br />
                                            {/* <CInput className="form-control" id="bcc7" type="email" placeholder="Type renewDate" value={this.state.userDetails.renewDate} /> */}
                                            <TextField
                                                fullWidth
                                                // helperText="Please specify the first name"
                                                label="renewDate"
                                                margin="dense"
                                                name="renewDate"
                                                onChange={this.onChangeRenewDate}
                                                required
                                                value={this.state.renewDate}
                                                variant="outlined"
                                            />
                                        </p>
                                    </CCol>
                                </CRow>
                                {/* <table className="table table-striped table-hover">
                  <tbody>
                    <tr><td>{userDetails.id}</td></tr>
                    <tr><td>{userDetails.name}</td></tr>
                    <tr><td>{userDetails.profile}</td></tr>
                    <tr><td>{userDetails.username}</td></tr>
                    <tr><td>{userDetails.DataRegistered}</td></tr>
                    <tr><td>{userDetails.membership}</td></tr>
                    <tr><td>{userDetails.role}</td></tr>
                    <tr><td>{userDetails.subscription}</td></tr>
                  </tbody>
                </table> */}
                            </CCardBody>
                            <CRow>
                                <CCol lg={10}>&nbsp;</CCol>
                                <CCol lg={1}><CButton color="light" disabled onClick={this.onRefoundConfirm}>Delete User </CButton></CCol>
                                <CCol lg={1}><CButton color="danger" onClick={this.onRefoundConfirm}>Ban User </CButton></CCol>
                            </CRow>
                            <br />
                        </CCard>

                    </CCol>

                </CRow>
            </div>
        )
    }
}

export default PlanVersion;


